import { VERT_ALIGN_TOGGLE_SUB, VERT_ALIGN_TOGGLE_SUP } from '../actions';
import toggleSubMark from '../commands/toggleSubMark';
import toggleSupMark from '../commands/toggleSupMark';

export default function onAction(action, editor, next) {
  const { type } = action;
  if (type === VERT_ALIGN_TOGGLE_SUB) {
    return toggleSubMark(editor);
  }
  if (type === VERT_ALIGN_TOGGLE_SUP) {
    return toggleSupMark(editor);
  }
  return next();
}
